@import url('https://fonts.googleapis.com/css2?family=Grape+Nuts&family=Norican&display=swap');
@import url("https://pro.fontawesome.com/releases/v6.0.0-beta1/css/all.css");
@import url("https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap");

.todolist{
    min-height: 60vh;
    padding: 20px;
    h1 {
      text-align: center;
      color: rgb(253, 96, 96);
    }
    
    ol {
    width: calc(min(100rem, 90%) * 1);
      margin-inline: auto;
    
      display: flex;
      flex-wrap: wrap;
      gap: 2rem;
    
      list-style: none;
      counter-reset: cardnr;
      justify-content: center;
    }
    
    li {
    transition: scale 0.3s ;
      --frontColor: white;
      --width: 14em;
      --inlineP: 0.5rem;
      --borderR: 4rem;
      --arrowW: 2rem;
      --arrowH: 1.5rem;
    
      counter-increment: cardnr;
      width: calc(var(--width) - var(--inlineP) * 2);
      display: grid;
      grid-template:
        "icon" var(--borderR)
        "title"
        "descr" 1fr;
      margin-inline: var(--inlineP);
      margin-bottom: calc(var(--borderR));
      position: relative;
      .icon, .title, .descr {
        background: var(--frontColor);
        padding-inline: 1rem;
        padding-bottom: 1rem;
      }
      .icon, .title {
        color: var(--accent-color);
        text-align: center;
        padding-bottom: 0.5rem;
      }
      .title, .descr {
        filter: drop-shadow(0.125rem 0.125rem 0.075rem rgba(0, 0, 0, 0.25));
      }
      .icon {
        grid-area: icon;
        font-size: 2rem;
        display: grid;
        place-items: center;
        border-radius: var(--borderR) 0 0 0;
        position: relative;
      }
      .title {
        grid-area: title;
        font-size: 1.1rem;
        font-weight: bold;
      }
      .descr {
        grid-area: descr;
        font-size: 0.85rem;
        text-align: center;
        &::before {
          content: "";
          width: var(--arrowW);
          height: var(--arrowH);
          position: absolute;
          right: 1.5rem;
          top: 100%;
          background: inherit;
          clip-path: polygon(0 0, 100% 0, 50% 100%);
        }
      }
      &::after {
        content: counter(cardnr, decimal-leading-zero);
        position: absolute;
        z-index: -1;
        left: calc(var(--inlineP) * -1);
        right: calc(var(--inlineP) * -1);
        top: var(--borderR);
        bottom: calc(var(--borderR) * -1);
        display: flex;
        align-items: flex-end;
        background: var(--accent-color);
        background-image: linear-gradient(
          160deg,
          rgba(255, 255, 255, 0.25),
          transparent 25% 75%,
          rgba(0, 0, 0, 0.25)
        );
        border-radius: 0 0 var(--borderR) 0;
        --pad: 1rem;
        padding: var(--pad);
        font-size: calc(var(--borderR) - var(--pad) * 2);
        color: white;
      }
      &::before {
        content: "";
        position: absolute;
        height: var(--borderR);
        top: calc(100% + var(--borderR) - 2px);
        left: calc(var(--inlineP) * -1);
        right: calc(var(--inlineP) * -1);
        border-radius: 0 var(--borderR) 0 0;
        background-image: linear-gradient(var(--accent-color), transparent 60%);
        opacity: 0.5;
        filter: blur(2px);
      }


      &:hover{
        cursor: pointer;
        scale: 1.1;
      }
    }
    
    
}
